.align-label{
  display: inline-block;
  width: 400px;
  font-size: 22px;
}
.align input{
  font-size: 22px;
  width: 15px;

}
.space{
margin: 15px;
}

.button{
  background-color: black;
  padding-left: 30px;
  padding-top:5px ;
  padding-bottom:5px ;
  padding-right: 30px;
  font-size: 22px;
  color: white;
}

.font{
  size: 22px;
}

.wrapper{
  height: 100vh;
box-sizing: border-box;
}
.header{
  height: 10%;
border: 2px red solid;
box-sizing: border-box;
}
.container{
  height: 90%;
border: 2px red solid;
box-sizing: border-box;
display: flex;
flex-direction: row;
}
.sidebar{
  width: 20%;
border: 2px red solid;
}
.maincontent{
  width: 80%;
border: 2px red solid;
}
.table-border{
  border: 2px solid black;
  border-collapse: collapse;
  text-align: center;
  

}
.t-width{
  width: 100%;

}
.profile-pic{
  border-radius: 75px;
    width: 60px;
}